import React, {PureComponent} from 'react';
import SignaturePad from 'react-signature-pad-wrapper';
import { Form, Row, Col, Button } from 'react-bootstrap';
import { CheckCircle, Check, X, PencilSquare } from 'react-bootstrap-icons';
import DirectionProvider, { DIRECTIONS } from 'react-with-direction/dist/DirectionProvider';
import htmlToImage from 'html-to-image';


class App extends PureComponent {

    handleClear() {
        this.signaturePad.instance.clear();
    }

    handleSave() {
        var node = document.getElementById('toSave');
        htmlToImage.toPng(node)
          .then(function (dataUrl) {
            var a = document.createElement("a");
            a.href = dataUrl;
            a.download = "Statement.png";
            a.click();
          })
          .catch(function (error) {
            console.error('oops, something went wrong!', error);
          });
    }

    renderTitle() {
        return (
            <div className="columns">
                <div className="column" style={{ display: 'flex', justifyContent: 'center' }}>
                    <h1 className="title" style={{ marginBottom: 20 }}><PencilSquare /> הצהרת בריאות</h1>
                </div>
            </div>
        );
    }

    renderForm() {
        return (
            <div>
                <Form>
                    <Form.Group as={Row} controlId="frmChildName">
                        <Form.Label column sm={2}>שם התלמיד/ה</Form.Label>
                        <Col sm={4}>
                            <Form.Control type="text" placeholder="לדוגמא: פלוני אלמוני" />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="frmChildID">
                        <Form.Label column sm={2}>מס׳ תעודת זהות</Form.Label>
                        <Col sm={4}>
                            <Form.Control type="text" placeholder="לדוגמא: 301234567" />
                        </Col>
                    </Form.Group>
                    <Row>
                        <Col sm={6}>
                            <span style={{ textDecorationLine: "underline", fontWeight: "bold" }}>אני (החתום מטה) מצהיר כי:</span>
                        </Col>
                    </Row>
                    <Row>
                        <Col sm={6}>
                            <CheckCircle /> מדדתי חום לילד/תי ונמצא כי חום גופו/ה מתחת ל-38 מעלות צלזיוס
                        </Col>
                    </Row>
                    <Row>
                        <Col sm={6}>
                            <CheckCircle /> ילד/תי לא משתעל/ת ואין לו/לה קשיים בנשימה*
                        </Col>
                    </Row>
                    <Row>
                        <Col sm={6}>
                            <CheckCircle /> למיטב ידיעתי ילד/תי לא היה/הייתה במגע קרוב עם חולה קורונה בשבועיים האחרונים
                        </Col>
                    </Row>

                    <Form.Group style={{ marginTop: 20 }} as={Row} controlId="frmParentName">
                        <Form.Label column sm={2}>שם ההורה</Form.Label>
                        <Col sm={4}>
                            <Form.Control type="text" placeholder="לדוגמא: אבא של פלוני" />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="frmParentId">
                        <Form.Label column sm={2}>מס׳ תעודת זהות</Form.Label>
                        <Col sm={4}>
                            <Form.Control type="text" placeholder="לדוגמא: 301234567" />
                        </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="frmDate">
                        <Form.Label column sm={2}>תאריך</Form.Label>
                        <Col sm={4}>
                            <Form.Control type="text" defaultValue={new Date().toLocaleDateString("he")} />
                        </Col>
                    </Form.Group>
                </Form>
            </div>
        )
    }

    renderSignaturePad() {
        return (
            <div className="columns">
                <div className="column">
                    <Form.Group as={Row} controlId="frmDate">
                        <Form.Label column sm={2}>חתימת ההורה</Form.Label>
                        <Col sm={4}>
                            <div className="card">
                                <div className="card-content">
                                    <div className="content">
                                        <SignaturePad
                                            redrawOnResize={true}
                                            ref={ref => this.signaturePad = ref} />
                                    </div>
                                </div>
                            </div>
                        </Col>
                    </Form.Group>
                </div>
            </div>
        );
    }

    renderClarifications() {
        return (
            <div className="columns">
                <div className="column">
                    <span>* למעט שיעול או קושי בנשימה הנובע ממצב כרוני כגון אסטמה או אלרגיה אחרת</span>
                </div>
            </div>
        );
    }

    renderSave() {
        return (
            <div className="columns" style={{ marginTop: 20, marginBottom: 20 }} >
                <div className="column" style={{ display: 'flex', justifyContent: 'space-between' }}>
                    <Button variant="outline-dark" onClick={this.handleClear.bind(this)}><X /> נקה חתימה</Button>
                    <Button variant="dark" onClick={this.handleSave.bind(this)}><Check /> שמור כתמונה לשיתוף</Button>
                </div>
            </div>
        )
    }

    render() {
        return (
            <DirectionProvider direction={DIRECTIONS.RTL}>
                <div>
                    <section className="section" id="toSave">
                        <div className="container">
                            {this.renderTitle()}
                            {this.renderForm()}
                            {this.renderSignaturePad()}
                            {this.renderClarifications()}
                        </div>
                    </section>
                    <section className="section">
                        <div className="container">
                            {this.renderSave()}
                        </div>
                    </section>
                </div>
            </DirectionProvider>
        );
    }
}

export default App;
